.cards{
    display: flex;
    justify-content: space-evenly;
}

.mainProduct{
    display: flex;
    height: 100vh;
}
.Image{
    width:48vw;
    padding:15px
}
.Details{
    width:48vw
}


@media (max-width:768px) {
    .mainProduct{
        display: block;
        font-size: 48%;
        height: 100%;
    }
    .Image{
        width:90vw;
        margin:0px;
        padding: 5px;
        
    }
    .Details{
        width:90vw;
        margin:0px
    }
    .butt{
        margin-top: 20px;
    }
}